.box-shadowed() {
  box-shadow: 0 2px 4px 0 #dee5f0;
}

.bg-image(@url) {
  background-image: url(@url);
  background-repeat: no-repeat;
  background-size: contain;
}

.ellipsis(@line: 1) {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: @line;
}
