@import '~@/style/var';

.infoBox {
  // margin: 10px 0;
  padding: 16px;
  background: #fafbfb;

  .title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    color: rgba(0, 0, 0, 0.65);
    font-weight: 600;
    font-size: 18px;

    line-height: 1 !important;
    letter-spacing: 0.32px;

    > div {
      padding: 2px 8px;
      font-weight: 400;
      font-size: 12px;
      line-height: 1.2;
      vertical-align: middle;
      border: 1px solid currentColor;
      border-radius: 14px;
    }

    .tagSuccess {
      color: @success-color;
    }

    .tagError {
      color: @yellow-color;
    }
  }

  :global {
    .ant-descriptions-item {
      padding-top: 5px;
    }

    .ant-form-item {
      margin-bottom: 10px;
    }

    .ant-divider {
      margin: 0;
    }

    .ant-form-item-label {
      color: @break-color3;
    }
  }
}

.inputItem {
  margin-bottom: 16px;
}
