.title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 20px;
}

.global-setting {
  position: relative;

  &-model {
    :global {
      .ant-modal-body {
        padding: 30px 40px 0;
      }

      .ant-modal-footer {
        padding: 0 40px 30px;
        border-top: none;
      }
    }
  }
}

.ext {
  position: absolute;
  width: 200px;
  bottom: 8px;
  left: 50px;
}